:root {
  /* COLORS */
  --white: rgba(255, 255, 255, 1);
  --gray: #333;
  --blue: #0367a6;
  --lightblue: rgba(255, 31, 53, 1);
  --button-radius: 0.7rem;
  /* SIZES */
  --max-width: 858px;
  --max-height: 620px;
  font-size: 16px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.bodyRegister {
  padding-top: 10.5em;
  display: grid;
  place-items: center;
  overflow-y: hidden;
}

.form__title {
  font-weight: 300;
}

.container {
  background-color: var(--white);
  border-radius: 41px;
  box-shadow: 0 0.9rem 1.7rem rgba(0, 0, 0, 0.25),
    0 0.7rem 0.7rem rgba(0, 0, 0, 0.22);
  height: var(--max-height);
  max-width: var(--max-width);
  overflow: hidden;
  position: relative;
  width: 100%;
  margin: 10px 0px 108px 0px;
}

.container__form {
  height: 100%;
  position: absolute;
  top: 0;
  transition: all 0.5s ease-in;
}

.container--signin {
  left: 0;
  width: 50%;
  z-index: 2;
}

.container.right-panel-active .container--signin {
  transform: translateX(100%);
}

.container--signup {
  left: 0;
  opacity: 0;
  width: 50%;
  z-index: 1;
}

.container.right-panel-active .container--signup {
  animation: show 0.5s;
  opacity: 1;
  transform: translateX(100%);
  z-index: 5;
}

.container__overlay {
  height: 100%;
  left: 50%;
  overflow: hidden;
  position: absolute;
  top: 0;
  transition: transform 0.5s ease-in-out;
  width: 50%;
  z-index: 50;
  border-radius: 41px;
  h3 {
    font-size: 2.3vw;
    color: #fff;
    margin: 15px 0px 1.1em 0px;
    font-family: inter;
  }
  P {
    color: #fff;
    margin-bottom: 3em;
    max-width: 14vw;
    font-family: IBM Plex Sans Hebrew;
    font-size: 1.2em;
  }
}

.container.right-panel-active .container__overlay {
  transform: translateX(-100%);
}

.overlay {
  background-color: var(--lightblue);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  left: -100%;
  position: relative;
  transform: translateX(0);
  transition: transform 0.5s ease-in-out;
  width: 200%;
}

.container.right-panel-active .overlay {
  transform: translateX(50%);
}

.overlay__panel {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  position: absolute;
  text-align: center;
  top: 0;
  transform: translateX(0);
  transition: transform 0.5s ease-in-out;
  width: 50%;
}

.overlay--left {
  transform: translateX(-20%);
}

.container.right-panel-active .overlay--left {
  transform: translateX(0);
}

.overlay--right {
  right: 0;
  transform: translateX(0);
}

.container.right-panel-active .overlay--right {
  transform: translateX(20%);
}

.btn {
  background-color: var(--blue);
  background-image: linear-gradient(
    90deg,
    var(--blue) 0%,
    var(--lightblue) 74%
  );
  border-radius: 20px;
  border: 1px solid var(--blue);
  color: var(--white);
  cursor: pointer;
  font-size: 0.8rem;
  font-weight: bold;
  letter-spacing: 0.1rem;
  padding: 0.9rem 4rem;
  text-transform: uppercase;
  transition: transform 80ms ease-in;
}

.form > .btn {
  margin-top: 1.5rem;
}

.btn:active {
  transform: scale(0.95);
}

.form {
  background-color: var(--white);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding-bottom: 1rem;
  height: 100%;
  text-align: center;
}
.formScroll {
  padding-top: 25em;
  overflow-y: scroll;
}

.input {
  background-color: #fff;
  border-radius: 10px;
  padding: 0.9rem 0.9rem;
  margin: 0.5rem 0;
  width: 90%;
}
@keyframes show {
  0%,
  49.99% {
    opacity: 0;
    z-index: 1;
  }
  50%,
  100% {
    opacity: 1;
    z-index: 5;
  }
}
@media screen and (max-width: 1441px) {
  .container {
    margin: 10px 0px 80px 0px;
    height: 72vh;
  }
}
@media screen and (max-width: 865px) {
  .container {
    margin: 10px 15px 80px 15px;
  }
}
